import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TimeTrackerService } from '../../time-tracker.service';

@Component({
    selector: 'app-time-tracker-report',
    templateUrl: './time-tracker-report.component.html',
    styleUrls: ['./time-tracker-report.component.scss']
})
export class TimeTrackerReportComponent implements OnInit {
    private displayPanel = 'monthly';
    constructor(private timeTrackerService: TimeTrackerService,
        private route: ActivatedRoute,
        private router: Router) {}
    ngOnInit() {
        if (this.timeTrackerService.setId(this.route.snapshot.paramMap.get('id'))) {
            this.timeTrackerService.getDailyTimesheet();
        }
    }
    generateReport(param: string) {
        this.displayPanel = param;
    }
    backToTrack() {
        this.router.navigate(['apps/time-tracker', this.route.snapshot.paramMap.get('id')]);
        return true;
    }
}