import { Injectable} from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class ToolsGlobalService {
    constructor(private http: HttpClient){}
    public toolsDataSource: BehaviorSubject<{ type: string, data: object }> = new BehaviorSubject({ type: '', data: {} });
    public toolsDataReceiver = this.toolsDataSource.asObservable();
    validateJsonUrl(param: string) {
        return /^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+$/.test(param);
    }
    httpFetch(jsonUrl: string) {
        this.http.get(jsonUrl).subscribe(jsonResponse => {
            this.toolsDataSource.next({type: 'RAW-JSON', data: jsonResponse});
            return true;
        })
    }
    httpUpdate(jsonUrl: string, jsonData: string) {
        try {
            JSON.parse(jsonData);
        }catch(e){
            alert('Invalid Json !')
            return false;
        }
        let headers: any = new HttpHeaders();
        headers = headers.set('Content-Type', 'application/json; charset=utf-8');
        this.http.put(jsonUrl, jsonData, {headers}).subscribe((res)=>{
            this.toolsDataSource.next({ type: 'UPDATED-JSON', data: res });
            return true;
        })
    }
}