import { Component } from '@angular/core';
import { TimeTrackerReportService } from '../time-tracker-report.service';

@Component({
    selector: 'app-time-tracker-report-daily',
    templateUrl: './time-tracker-report-daily.component.html',
    styleUrls: ['./time-tracker-report-daily.component.scss']
})

export class TimeTrackerReportDailyComponent {
    private dayCounter: number = 0;
    private monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    private today: string = this.getToday(new Date());
    private dailyLog = null;
    constructor(private timeTrackerReportService: TimeTrackerReportService) {
        this.dailyLog = this.timeTrackerReportService.getTimesheet('day', new Date());
        this.dailyLog = this.timeTrackerReportService.getDayDetails(this.dailyLog, new Date());
    }
    ngOnInit() {}
    calNav(type: string, action: string) {
        let date = new Date();
        switch(true) {
            case (type == 'day' && action == 'prev'):
                this.dayCounter -= 1;
                date.setDate(date.getDate() + this.dayCounter);
                this.today = this.getToday(date);
                this.dailyLog = this.timeTrackerReportService.getTimesheet('day', date);
                this.dailyLog = this.timeTrackerReportService.getDayDetails(this.dailyLog, date);
                break;
            case (type == 'day' && action == 'next'):
                this.dayCounter += 1;
                date.setDate(date.getDate() + this.dayCounter);
                this.today = this.getToday(date);
                this.dailyLog = this.timeTrackerReportService.getTimesheet('day', date);
                this.dailyLog = this.timeTrackerReportService.getDayDetails(this.dailyLog, date);
                break;
        }
        return true;
    }
    getToday(date: Date) {
        return this.monthNames[date.getMonth()] + ' ' + date.getDate() + ', ' + date.getFullYear();
    }
}