import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class TimeTrackerService {
    jsonId: string = null;
    now = null;
    todayDate: string = '';
    dummyObj = { 'insideOffice': { 'entry': '', 'exit': '' }, 'recent': { 'start': '' }, 'breaks': [] };
    public dataSource: BehaviorSubject<any> = new BehaviorSubject(null);
    public receiveData = this.dataSource.asObservable();
    constructor(private http: HttpClient) { }
    getTime() {
        this.now = new Date();
        this.todayDate = this.now.getFullYear() + '-' + this.now.getMonth() + '-' + this.now.getDate();
        return true;
    }
    private dailyTimesheet: Object = {};
    setId(id: string) {
        this.jsonId = id;
        return true;
    }
    getDailyTimesheet(type?: string): Promise<any> {
        if(!type) {
            type = '';
        }
        /*if (this.dummyObj.url) { //this one set while creating fresh id
            delete this.dummyObj.url;
        }
        if (this.dummyObj.type) { //this one set while creating fresh id
            delete this.dummyObj.type;
        }*/
        this.getTime();
        return new Promise((resolve, reject) => {
            if (/--http\:\/\/localhost\:/.test(window.location.href)) {
                this.http.get('../../assets/data/sample.json').subscribe(data => {
                    resolve(data);
                });
            } else {
                this.http.get('https://api.myjson.com/bins/' + this.jsonId).subscribe(data => {
                    resolve(data);
                });
            }
        }).then(data => {
            this.dailyTimesheet = data;
            this.dailyTimesheet[this.todayDate] = (this.dailyTimesheet[this.todayDate] == undefined) ? this.dummyObj : this.dailyTimesheet[this.todayDate];
            if (type != 'none') {
                this.dataSource.next({ today: this.dailyTimesheet[this.todayDate], full: this.dailyTimesheet, type: type });
            }
            return this.dailyTimesheet[this.todayDate];
        });
    }
    swipeIn() {
        this.getTime();
        new Promise((resolve, reject) => {
            resolve(this.getDailyTimesheet('none'))
        }).then(() => {
            if (this.dailyTimesheet[this.todayDate] && this.dailyTimesheet[this.todayDate].insideOffice.exit != '') {
                this.dailyTimesheet[this.todayDate].breaks.push({
                    'start': this.dailyTimesheet[this.todayDate].insideOffice.exit,
                    'end': this.now.getTime()
                });
                this.dailyTimesheet[this.todayDate].insideOffice.exit = '';
            } else if (this.dailyTimesheet[this.todayDate] && this.dummyObj) {
                if (this.dailyTimesheet[this.todayDate].insideOffice &&
                    this.dailyTimesheet[this.todayDate].insideOffice.entry != '') {
                } else {
                    this.dailyTimesheet[this.todayDate].insideOffice.entry = this.dummyObj.insideOffice.entry = this.now.getTime();
                }
            }
            this.jsonUpdate();
        }).then(() => {
            return this.dailyTimesheet[this.todayDate];
        });
    }
    swipeOut() {
        this.getTime();
        new Promise((resolve, reject) => {
            resolve(this.getDailyTimesheet('none'))
        }).then(() => {
        if (this.dailyTimesheet[this.todayDate].recent.start != '') {
            this.dailyTimesheet[this.todayDate].breaks.push({
                'start': this.dailyTimesheet[this.todayDate].recent.start,
                'end': this.now.getTime()
            });
            this.dailyTimesheet[this.todayDate].recent.start = '';
        }
        this.dailyTimesheet[this.todayDate].insideOffice.exit = this.now.getTime();
        this.jsonUpdate();
        }).then(() => {
            return this.dailyTimesheet[this.todayDate];
        });
    }
    break() {
        this.getTime();
        new Promise((resolve, reject) => {
            resolve(this.getDailyTimesheet('none'));
        }).then(() => {
            if (this.dailyTimesheet[this.todayDate].recent.start == '') {
                this.dailyTimesheet[this.todayDate].recent.start = this.now.getTime();
            } else {
                this.dailyTimesheet[this.todayDate].breaks.push({
                    'start': this.dailyTimesheet[this.todayDate].recent.start,
                    'end': this.now.getTime()
                });
                this.dailyTimesheet[this.todayDate].recent.start = '';
            }
            this.jsonUpdate();
        });
        return true;
    }
    jsonUpdate() {
        if (/--http\:\/\/localhost\:/.test(window.location.href)) {
            return false;
        }
        let headers: any = new HttpHeaders();
        headers = headers.set('Content-Type', 'application/json; charset=utf-8');
        this.http.put('https://api.myjson.com/bins/' + this.jsonId,
            JSON.stringify(this.dailyTimesheet),
            { headers }
        ).subscribe(res => {
            this.dailyTimesheet[this.todayDate] = res[this.todayDate];
            this.dataSource.next(res[this.todayDate]);
            return true;
        });
    }
    jsonCreate() {
        if (/--http\:\/\/localhost\:/.test(window.location.href)) {
            return false;
        }
        let headers: any = new HttpHeaders();
        headers = headers.set('Content-Type', 'application/json; charset=utf-8');
        this.http.post('https://api.myjson.com/bins',
            '{}',
            { headers }
        ).subscribe(res => {
            let tmpData: any = JSON.parse(JSON.stringify(this.dummyObj));
            tmpData.url = res;
            tmpData.type = 'generate-code';
            this.dataSource.next(tmpData);
            return true;
        });
    }
    
}