import { Component, OnInit } from '@angular/core';
import { ToolsGlobalService } from '../../tools-global.service';

@Component({
  selector: 'app-online-free-db',
  templateUrl: './online-free-db.component.html',
  styleUrls: ['./online-free-db.component.scss']
})
export class OnlineFreeDbComponent implements OnInit {
  constructor(private toolsGlobalService: ToolsGlobalService) { }
  txtUrl = 'https://api.myjson.com/bins/';
  txtContent = '';
  ngOnInit() {
    this.toolsGlobalService.toolsDataReceiver.subscribe(param => {
      if (param.type == 'RAW-JSON' || param.type == 'UPDATED-JSON') {
        this.txtContent = JSON.stringify(param.data, null, 4);
      }
    })
  }
  fetch(event) {
    if (typeof event != 'undefined') {
      if(event.code != 'Enter') {
        return;
      }
    }
    if (this.toolsGlobalService.validateJsonUrl(this.txtUrl)) {
      this.toolsGlobalService.httpFetch(this.txtUrl);
    } else {
      alert('Invalid Url !');
    }
  }
  minify() {
    this.txtContent = JSON.stringify(JSON.parse(this.txtContent));
    return this.txtContent;
  }
  format() {
    this.txtContent = JSON.stringify(JSON.parse(this.txtContent), null, 4);
    return true;
  }
  update(param: any) {
    if (param == 'delete' && !confirm('Are you sure to erase content ?')) {
      return false;
    } else if (!confirm('Are you sure to update content ?')) {
      return false;
    }
    try {
      JSON.parse(this.txtContent);
    } catch (e) {
      alert('Invalid Json !')
      return false;
    }
    if (param == 'delete') {
      this.txtContent = '{}';
    }
    this.toolsGlobalService.httpUpdate(this.txtUrl, JSON.stringify(JSON.parse(this.txtContent)));
  }
}