import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TimeTrackerService } from './time-tracker.service';

@Component({
  selector: 'app-time-tracker',
  templateUrl: './time-tracker.component.html',
  styleUrls: ['./time-tracker.component.scss']
})
export class TimeTrackerComponent implements OnInit {
  uniqueCode:string = null;
  constructor(private router: Router,
    private timeTrackerService: TimeTrackerService){}
  login() {
    this.router.navigate(['apps/time-tracker', this.uniqueCode]);
  }
  ngOnInit() {
    this.timeTrackerService.receiveData.subscribe(res => {
      let data = (res && res.today) ? res.today : res;
      if (data && data.type == 'generate-code' && data.url['uri']) {
        this.uniqueCode = data.url['uri'].split('/')[4];
        alert('Your New Code: ' + this.uniqueCode + "\n Save it at safe place !");
      }
    });
  }
  setUniqueCode(event) {
    if (event.which == 13) {
      this.login();
    }
  }
  generateUniqueCode() {
    if(confirm('Are you sure to generate code ?')) {
      this.timeTrackerService.jsonCreate();
    }
  }
}
