import { Component, OnInit } from '@angular/core';
import { TimeTrackerService } from '../../time-tracker.service';
import { ActivatedRoute, Router } from '@angular/router'

@Component({
  selector: 'app-time-tracker-interact',
  templateUrl: './time-tracker-interact.component.html',
  styleUrls: ['./time-tracker-interact.component.scss']
})
export class TimeTrackerInteractComponent implements OnInit {
  loader = false;
  buttonStatus: Object = {
    entry: true,
    in: false,
    out: false,
    exit: false
  };
  staticContent: Object = {
    button: {
      entry: 'Login',
      exit: 'Logout',
      breakStart: 'Swipe Out',
      breakEnd: 'Swipe In'
    }
  };
  constructor(private timeTrackerService: TimeTrackerService,
              private route: ActivatedRoute,
              private router: Router) { }
  ngOnInit() {
    this.timeTrackerService.receiveData.subscribe(res => {
      let data = (res && res.today) ? res.today : res;
      if (typeof data === 'object' && data) {
        switch (true) {
          case (data['insideOffice'].entry == ''):
            this.buttonStatus = this.swipeOut();
            break;
          case (data['insideOffice'].exit != ''):
            this.buttonStatus = this.swipeOut();
            break;
          case (data['recent'].start != ''):
            this.buttonStatus = this.breakStart();
            break;
          case (data['recent'].start == ''):
            this.buttonStatus = this.swipeIn();
            break;
        }
        this.loader = true;
      }
    });
    setTimeout(() => {
      this.loader = true;
    }, 3000);
  }
  ngOnChanges() {
  }
  swipeIn() {
    return {
      entry: false,
      in: false,
      out: true,
      exit: true
    };
  }
  breakStart() {
    return {
      entry: false,
      in: true,
      out: false,
      exit: true
    };
  }
  breakEnd() {
    return {
      entry: false,
      in: false,
      out: true,
      exit: true
    };
  }
  swipeOut() {
    return {
      entry: true,
      in: false,
      out: false,
      exit: false
    };
  }
  track(param: string) {
    switch (param) {
      case 'entry':
        this.timeTrackerService.swipeIn();
        break;
      case 'breakstart':
        this.timeTrackerService.break();
        break;
      case 'breakend':
        this.timeTrackerService.break();
        break;
      case 'exit':
        this.timeTrackerService.swipeOut();
        break;
    }
  }
  redirectToReport() {
    this.route.snapshot.paramMap.get('id')
    this.router.navigate(['apps/time-tracker', this.route.snapshot.paramMap.get('id'), 'report'])
  }
}