import { Component } from '@angular/core';
import { TimeTrackerService } from '../../../time-tracker.service';
import { TimeTrackerReportService } from '../time-tracker-report.service';

@Component({
    selector: 'app-time-tracker-report-weekly',
    templateUrl: './time-tracker-report-weekly.component.html',
    styleUrls: ['./time-tracker-report-weekly.component.scss']
})

export class TimeTrackerReportWeeklyComponent {
    private displayPanel = 'monthly';
    private todayDetail = null;
    private week = null;
    private monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    private currentMonthYear = '';
    private weekCounter = 0;
    constructor(private timeTrackerReportService: TimeTrackerReportService,
        private timeTrackerService: TimeTrackerService) { }
    ngOnInit() {
        this.timeTrackerService.receiveData.subscribe(res => {
            this.calNav('week', 'now');
        });
    }
    fetchMonthData(year: number, month: number) {
        this.week = this.timeTrackerReportService.makeCalender(year, month);
    }
    generateReport(param: string) {
        this.displayPanel = param;
        this.week = this.timeTrackerReportService.getWeekData(new Date().getFullYear(), new Date().getMonth(), new Date().getDate());
    }
    calNav(type: string, method: string) {
        let date = new Date();
        switch (true) {
            case (type == 'week' && method == 'prev'):
                this.weekCounter = this.weekCounter - 1;
                date.setDate(date.getDate() + (this.weekCounter * 7));
                this.week = this.timeTrackerReportService.getWeekData(date.getFullYear(), date.getMonth(), date.getDate());
                this.currentMonthYear = this.week[1][0].date + ' - ' + this.week[1][6].date;
                break;
            case (type == 'week' && method == 'next'):
                this.weekCounter = this.weekCounter + 1;
                date.setDate(date.getDate() + (this.weekCounter * 7));
                this.week = this.timeTrackerReportService.getWeekData(date.getFullYear(), date.getMonth(), date.getDate());
                this.currentMonthYear = this.week[1][0].date + ' - ' + this.week[1][6].date;
                break;
            case (type == 'week' && method == 'now'):
                this.weekCounter = 0;
                date.setMonth(date.getMonth() + this.weekCounter);
                this.week = this.timeTrackerReportService.getWeekData(date.getFullYear(), date.getMonth(), date.getDate());
                this.currentMonthYear = this.week[1][0].date + ' - ' + this.week[1][6].date;
                break;
        }
    }
}