import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OnlineFreeDbComponent } from './components/tools/json/online-free-db/online-free-db.component';
import { TimeTrackerComponent } from './components/apps/time-tracker/time-tracker.component';
import { HomeComponent } from './components/home/home.component';
import { TimeTrackerDashboardComponent } from './components/apps/time-tracker/time-tracker-dashboard/time-tracker-dashboard.component';
import { InvalidComponent } from './components/invalid/invalid.component';
import { timestampToolsComponent } from './components/tools/clock/timestamp/timestamp-tools.component';
import { TimeTrackerReportComponent } from './components/apps/time-tracker/time-tracker-dashboard/time-tracker-report/time-tracker-report.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'tools/json/online-db', component: OnlineFreeDbComponent},
  { path: 'tools/clock/timestamp', component: timestampToolsComponent },
  { path: 'apps/time-tracker', component: TimeTrackerComponent},
  { path: 'apps/time-tracker/:id', component: TimeTrackerDashboardComponent },
  { path: 'apps/time-tracker/:id/report', component: TimeTrackerReportComponent },
  { path: '**', component: InvalidComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
