import { Component, OnInit } from '@angular/core';
import { TimeTrackerService } from '../../../time-tracker.service';
import { TimeTrackerReportService } from '../time-tracker-report.service';

@Component({
  selector: 'app-time-tracker-report-monthly',
  templateUrl: './time-tracker-report-monthly.component.html',
  styleUrls: ['./time-tracker-report-monthly.component.scss']
})
export class TimeTrackerReportMonthlyComponent implements OnInit {
  private displayPanel = 'monthly';
  private todayDetail = null;
  private calender = null;
  private monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  private currentMonthYear = '';
  private monthCounter = 0;
  constructor(private timeTrackerReportService: TimeTrackerReportService,
    private timeTrackerService: TimeTrackerService) {}
  ngOnInit() {
    this.timeTrackerService.receiveData.subscribe(res => {
      this.calNav('month', 'now');
    });
  }
  fetchMonthData(year: number, month: number) {
    this.calender = this.timeTrackerReportService.makeCalender(year, month);
  }
  generateReport(param: string) {
    this.displayPanel = param;
    this.calender = this.timeTrackerReportService.makeCalender(new Date().getFullYear(), new Date().getMonth());
  }
  calNav(type: string, method: string) {
    let date = new Date();
    switch (true) {
      case (type == 'month' && method == 'prev'):
        this.monthCounter = this.monthCounter - 1;
        date.setMonth(date.getMonth() + this.monthCounter);
        this.calender = this.timeTrackerReportService.makeCalender(date.getFullYear(), date.getMonth());
        this.currentMonthYear = this.monthNames[date.getMonth()] + ', ' + date.getFullYear();
        break;
      case (type == 'month' && method == 'next'):
        this.monthCounter = this.monthCounter + 1;
        date.setMonth(date.getMonth() + this.monthCounter);
        this.calender = this.timeTrackerReportService.makeCalender(date.getFullYear(), date.getMonth());
        this.currentMonthYear = this.monthNames[date.getMonth()] + ', ' + date.getFullYear();
        break;
      case (type == 'month' && method == 'now'):
        this.monthCounter = 0;
        date.setMonth(date.getMonth() + this.monthCounter);
        this.calender = this.timeTrackerReportService.makeCalender(date.getFullYear(), date.getMonth());
        this.currentMonthYear = this.monthNames[date.getMonth()] + ', ' + date.getFullYear();
        break;
    }
  }

}
