import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import { TimeTrackerService } from '../time-tracker.service';

@Component({
  selector: 'app-time-tracker-dashboard',
  templateUrl: './time-tracker-dashboard.component.html',
  styleUrls: ['./time-tracker-dashboard.component.scss']
})
export class TimeTrackerDashboardComponent implements OnInit {
  constructor(private timeTrackerService: TimeTrackerService,
    private route: ActivatedRoute) { }
  todayDetail: object = null;
  ngOnInit() {
    if (this.timeTrackerService.setId(this.route.snapshot.paramMap.get('id'))) {
      this.timeTrackerService.getDailyTimesheet()
        .then(data => {
          this.todayDetail = data;
        });
    }
  }
  receiver(data) {
    console.log(data);
  }
}
